import Axios from 'axios'

const request = Axios.create({
  baseURL: 'http://localhost:8080/api/v1',
  headers: {
    'Access-Control-Allow-Origin': 'http://localhost:8080'
  }
})

export default {
  get (context, url, data, then, loading = true) {
    if (data === undefined) data = new URLSearchParams()
    if (loading) context.$emit('loadingStart')
    return request.get(url, {params: data})
      .then(response => {
        return response.data
      })
      .then(data => {
        return then(data)
      })
      .catch(e => {
        console.log(e)
      })
      .finally(() => {
        if (loading) context.$emit('loadingEnd')
      })
  },
  post (context, url, data, then) {
    if (data === undefined) data = {}
    context.$emit('loadingStart')
    return request.post(url, data) // , {headers: {'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8'}}
      .then(response => {
        return response.data
      })
      .then(data => {
        return then(data)
      })
      .catch(e => {
        console.log(e)
      })
      .finally(() => {
        context.$emit('loadingEnd')
      })
  }
}



