<template>
  <v-flex xs12 sm6 md4 lg3>
    <v-card>
      <v-card-text>
        <v-menu bottom left>
          <v-btn class="btn__menu" icon slot="activator">
            <v-icon>more_vert</v-icon>
          </v-btn>
          <v-list>
            <v-list-tile v-for="(item, i) in menuItems" :key="i" @click="clickMenu(mBook.id, item.action)">
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
        <p>Название:
          <router-link :to="'/books/'+mBook.id">{{mBook.name}}</router-link>
        </p>
        <p>Год издания: {{mBook.year}}</p>
        <p>Автор:
          <router-link :to="'/authors/'+ mBook.author.id">{{mBook.author.fio}}</router-link>
        </p>
        <p>Издательство:
          <router-link :to="'/publishers/'+ mBook.publisher.id">{{mBook.publisher.name}}</router-link>
        </p>
        <p v-if="mBook.inStock" class="inStockTrue">В наличии</p>
        <p v-else class="inStockFalse">На руках</p>
      </v-card-text>
    </v-card>
    <v-dialog v-model="showFormDialog" max-width="500px" :onclose="reset">
      <v-card>
        <v-card-title>
          <h2>Редактирование книги</h2>
          <v-form ref="form">
            <v-text-field autofocus label="Название" v-model="bookEditable.name"></v-text-field>
            <v-text-field type="number" label="Год издания" v-model="bookEditable.year"></v-text-field>
            <v-select :items="authors" v-model="bookEditable.author" label="Автор" class="input-group--focused"
                      item-value="id" item-text="fio" return-object></v-select>
            <v-select :items="publishers" v-model="bookEditable.publisher" label="Издательство"
                      class="input-group--focused" item-text="name" item-value="id" return-object></v-select>
            <v-checkbox label="В наличии" v-model="bookEditable.inStock"></v-checkbox>
          </v-form>
        </v-card-title>
        <v-card-actions>
          <v-btn color="success" flat @click="save">Сохранить</v-btn>
          <v-spacer/>
          <v-btn color="primary" flat @click="showFormDialog=false">Закрыть</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-flex>

</template>

<script>
import AuthorsService from '../../services/AuthorsService'
import PublisherService from '../../services/PublisherService'
import BookService from '../../services/BookService'

export default {
  props: {
    book: {
      id: '',
      name: '',
      inStock: '',
      author: {
        id: '',
        fio: ''
      },
      publisher: {
        id: '',
        name: ''
      }
    }
  },
  data () {
    return {
      mBook: copy(this.book),
      menuItems: [
        {
          title: 'Редактировать',
          action: 'edit'
        },
        {
          title: 'Удалить',
          action: 'delete'
        }
      ],
      showFormDialog: false,
      bookEditable: {
        id: '',
        name: '',
        inStock: '',
        author: {
          id: '',
          fio: ''
        },
        publisher: {
          id: '',
          name: ''
        }
      },
      authors: [],
      publishers: []
    }
  },
  methods: {
    clickMenu (id, action) {
      if (action === 'edit') {
        AuthorsService.getFullList(this, (date) => {
          this.authors = date
        })
        PublisherService.getFullList(this, (date) => {
          this.publishers = date
        })
        this.bookEditable = copy(this.mBook)
        this.showFormDialog = true
      } else if (action === 'delete') {
        BookService.delete(this, this.mBook.id, (data) => {
          this.$emit('deleted')
        })
      }
    },
    reset () {
      this.$refs.form.reset()
    },
    save () {
      let context = this
      this.mBook = copy(this.bookEditable)
      BookService.save(context, context.mBook, (data) => {
      })
      this.showFormDialog = false
    }
  },
  watch: {
    book: function (newVal, oldVal) {
      this.mBook = copy(newVal)
    }
  }
}

function copy (object) {
  return JSON.parse(JSON.stringify(object))
}
</script>

<style lang="scss" scoped>
.inStockFalse {
  padding: 0 10px;
  background: #E53935;
  color: #fff;
}

.inStockTrue {
  padding: 0 10px;
  background: #43A047;
  color: #fff;
}

.menu {
  position: absolute;
  right: 0;
  top: 0;
}

form {
  width: 100%;
}
</style>



