<template>
  <v-container fluid grid-list-lg>
    <vue-headful :title="pageTitle"/>
    <h1>{{pageTitle}}</h1>
    <div class="text-xs-center" v-show="page.totalPages>1">
      <v-pagination :length="page.totalPages" v-model="currentPage" @input="getList"></v-pagination>
    </div>
    <v-layout row wrap>
      <v-flex xs12 sm6 md4 lg3 v-for="(item, index) in page.content" :key="index">
        <v-card>
          <v-card-text>
            <p>ФИО:
              <router-link :to="'/authors/'+item.id">{{item.fio}}</router-link>
            </p>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import AuthorsService from '../../../services/AuthorsService'

export default {
  computed: {
    pageTitle () {
      return 'Авторы'
    }
  },
  data () {
    return {
      page: {
        items: [],
        totalPages: 2
      },
      currentPage: 1
    }
  },
  methods: {
    getList () {
      let context = this
      AuthorsService.getList(context, context.currentPage, (data) => {
        context.page = data
      })
    }
  },
  beforeMount () {
    this.getList()
  }
}
</script>

<style lang="scss" scoped>

</style>



