import ajax from '../scripts/ajax'

export default {
  getList (context, page, then) {
    return ajax.get(context, '/authors/list?page=' + page, {}, (data) => {
      return then(data)
    })
  },
  getFullList (context, then) {
    return ajax.get(context, '/authors/list/full', {}, (data) => {
      return then(data)
    })
  },
  getItem (context, id, then) {
    return ajax.get(context, '/authors/' + id, {}, (data) => {
      return then(data)
    })
  },
  search (context, title, then) {
    return ajax.get(context, '/authors?title=' + title, {}, (data) => {
      return then(data)
    }, false)
  }
}



