<template>
  <v-container fluid grid-list-lg>
    <vue-headful :title="pageTitle"/>
    <h1>{{pageTitle}}</h1>
    <p>Название:
      <router-link :to="'/books/'+book.id">{{book.name}}</router-link>
    </p>
    <p>Год издания: {{book.year}}</p>
    <p>Автор:
      <router-link :to="'/authors/'+ book.author.id">{{book.author.fio}}</router-link>
    </p>
    <p>Издательство:
      <router-link :to="'/publishers/'+ book.publisher.id">{{book.publisher.name}}</router-link>
    </p>
    <p>Наличие: {{book.inStock}}</p>
    <h2>Место</h2>
    <p>Ряд: {{location.row}}</p>
    <p>Полка: {{location.shelf}}</p>
    <p>Номер: {{location.number}}</p>
  </v-container>
</template>

<script>
import BookService from '../../../services/BookService'
import Book from '../../parts/Book'

export default {
  props: ['bookId'],
  computed: {
    pageTitle () {
      return this.book.name
    }
  },
  data () {
    return {
      book: {
        id: '',
        name: '',
        year: '',
        isInStock: '',
        author: {
          id: '',
          fio: ''
        },
        publisher: {
          id: '',
          name: ''
        }
      },
      location: {
        id: '',
        row: '',
        shelf: '',
        number: ''
      }
    }
  },
  methods: {
    getItem () {
      let context = this
      BookService.getItem(context, context.bookId, (data) => {
        context.book = data.book
        context.location = data.location
      })
    }
  },
  beforeMount () {
    this.getItem()
  },
  components: {
    Book
  }
}
</script>

<style lang="scss" scoped>

</style>



