import ajax from '../scripts/ajax'

export default {
  getList (context, page, then) {
    return ajax.get(context, '/books/list?page=' + page, {}, (data) => {
      return then(data)
    })
  },
  getItem (context, id, then) {
    return ajax.get(context, '/books/' + id, {}, (data) => {
      return then(data)
    })
  },
  getListByPublisher (context, page, publisherId, then) {
    return ajax.get(context, '/books/list?page=' + page + '&publisherId=' + publisherId, {}, (data) => {
      return then(data)
    })
  },
  getListByAuthor (context, page, authorId, then) {
    return ajax.get(context, '/books/list?page=' + page + '&authorId=' + authorId, {}, (data) => {
      return then(data)
    })
  },
  save (context, book, then) {
    return ajax.post(context, '/books/save', book, (data) => {
      return then(data)
    })
  },
  delete (context, id, then) {
    return ajax.post(context, '/books/delete', {id: id}, (data) => {
      return then(data)
    })
  },
  search (context, page, oSearch, then) {
    return ajax.get(context, '/books/search?page=' + page, oSearch, (data) => {
      return then(data)
    }, false)
  }
}



