<template>
  <div id="app">
    <v-app>
      <Toolbar app/>
      <v-content>
        <transition>
          <router-view app v-on:loadingStart="loadStart" v-on:loadingEnd="loadEnd"/>
        </transition>
      </v-content>
    </v-app>
  </div>
</template>

<script>
import Toolbar from './components/parts/Toolbar'

export default {
  name: 'App',
  components: {
    Toolbar
  },
  data () {
    return {
      meta: {
        loading: false,
        loadingCount: 0
      }
    }
  },
  methods: {
    loadStart () {
      this.meta.loadingCount++
      this.meta.loading = true
    },
    loadEnd () {
      this.meta.loadingCount--
      if (this.meta.loadingCount < 1) {
        this.meta.loadingCount = 0
        this.meta.loading = false
      }
    }
  }
}
</script>

<style lang="scss">
.d-none {
  display: none !important;
}
</style>

<style lang="scss" scoped>
.content {
  /deep/ .content--wrap {
    position: relative;
  }
}

.progress-bar {
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  .progress-circular {
    margin: auto;
  }
}
</style>



