<template>
  <v-container fluid grid-list-lg>
    <vue-headful :title="pageTitle"/>
    <h1>{{pageTitle}}
      <v-btn color="success" @click="addBook">Добавить</v-btn>
    </h1>
    <div class="text-xs-center" v-show="page.totalPages>1">
      <v-pagination :length="page.totalPages" v-model="currentPage" @input="pageChanged"></v-pagination>
    </div>
    <v-layout row wrap>
      <Book :book="item" v-for="(item, index) in page.content" :key="index" @deleted="deleted"/>
    </v-layout>
    <v-dialog v-model="showFormDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <h2>Добавление книги</h2>
          <v-form ref="form">
            <v-text-field autofocus label="Название" v-model="bookEditable.name"></v-text-field>
            <v-text-field type="number" label="Год издания" v-model="bookEditable.year"></v-text-field>
            <v-select :items="authors" v-model="bookEditable.author" label="Автор" class="input-group--focused"
                      item-value="id" item-text="fio" return-object></v-select>
            <v-select :items="publishers" v-model="bookEditable.publisher" label="Издательство"
                      class="input-group--focused" item-text="name" item-value="id" return-object></v-select>
            <v-checkbox label="В наличии" v-model="bookEditable.inStock"></v-checkbox>
          </v-form>
        </v-card-title>
        <v-card-actions>
          <v-btn color="success" flat @click="save">Сохранить</v-btn>
          <v-spacer/>
          <v-btn color="primary" flat @click="showFormDialog=false">Закрыть</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import bookService from '../../../services/BookService'
import Book from '../../parts/Book'
import AuthorsService from '../../../services/AuthorsService'
import PublisherService from '../../../services/PublisherService'

export default {
  computed: {
    pageTitle () {
      return 'Книги'
    }
  },
  data () {
    return {
      page: {
        content: [],
        totalPages: 2
      },
      currentPage: 0,
      mBook: {
        id: 0,
        name: '',
        inStock: true,
        author: {
          id: '',
          fio: ''
        },
        publisher: {
          id: '',
          name: ''
        }
      },
      menuItems: [
        {
          title: 'Редактировать',
          action: 'edit'
        },
        {
          title: 'Удалить',
          action: 'delete'
        }
      ],
      showFormDialog: false,
      bookEditable: {
        id: '',
        name: '',
        inStock: '',
        author: {
          id: '',
          fio: ''
        },
        publisher: {
          id: '',
          name: ''
        }
      },
      authors: [],
      publishers: []
    }
  },
  methods: {
    deleted () {
      this.getList()
    },
    getList () {
      let context = this
      bookService.getList(context, context.currentPage, (data) => {
        context.page = data
      })
    },
    pageChanged () {
      if (this.currentPage > 1) {
        this.$router.push({path: '/', query: {page: this.currentPage}})
      } else {
        this.$router.push({path: '/'})
      }
      this.getList()
    },
    addBook () {
      AuthorsService.getFullList(this, (date) => {
        this.authors = date
      })
      PublisherService.getFullList(this, (date) => {
        this.publishers = date
      })
      this.bookEditable = copy(this.mBook)
      this.showFormDialog = true
    },
    save () {
      let context = this
      bookService.save(context, this.bookEditable, (data) => {
      })
      this.showFormDialog = false
    }
  },
  beforeMount () {
    this.currentPage = (this.$route.query.page === undefined) ? 1 : parseInt(this.$route.query.page)
    this.getList()
  },
  components: {
    Book
  }
}

function copy (object) {
  return JSON.parse(JSON.stringify(object))
}
</script>

<style lang="scss" scoped>
form {
  width: 100%;
}
</style>



