<template>
  <v-container fluid grid-list-lg>
    <vue-headful :title="pageTitle"/>
    <div class="text-xs-center">
      <v-text-field autofocus ref="searchInput" label="Название книги" @input="search" v-model="oSearch.name"/>
      <v-text-field ref="searchInput" label="ФИО Автора" @input="search" v-model="oSearch.author"/>
      <v-text-field ref="searchInput" label="Год издания от" @input="search" v-model="oSearch.dateStart"/>
      <v-text-field ref="searchInput" label="Год издания до" @input="search" v-model="oSearch.dateEnd"/>
      <v-text-field ref="searchInput" label="Издательство" @input="search" v-model="oSearch.publisher"/>
    </div>
    <div class="text-xs-center" v-show="page.totalPages>1">
      <v-pagination :length="page.totalPages" v-model="currentPage" @input="pageChanged"></v-pagination>
    </div>
    <v-layout row wrap>
      <Book :book="item" v-for="(item, index) in page.content" :key="index"/>
    </v-layout>
  </v-container>
</template>

<script>
import bookService from '../../services/BookService'
import Book from '../parts/Book'

export default {
  computed: {
    pageTitle () {
      return 'Поиск'
    }
  },
  data () {
    return {
      page: {
        items: [
          {
            name: String,
            publisher: {
              name: String
            },
            author: {
              fio: String
            }
          }
        ],
        totalPages: 2
      },
      currentPage: 1,
      oSearch: {
        name: '',
        author: '',
        publisher: '',
        dateStart: '',
        dateEnd: ''
      }
    }
  },
  methods: {
    search () {
      let context = this
      bookService.search(context, context.currentPage, context.oSearch, (data) => {
        context.page = data
      })
    },
    pageChanged () {
      if (this.currentPage > 1) {
        this.$router.push({path: '/search', query: {page: this.currentPage}})
      } else {
        this.$router.push({path: '/search'})
      }
      this.search()
    }
  },
  components: {
    Book
  },
  beforeMount () {
    this.currentPage = (this.$route.query.page === undefined) ? 1 : parseInt(this.$route.query.page)
    this.search()
  }
}
</script>

<style lang="scss" scoped>

</style>



