import Vue from 'vue'
import Router from 'vue-router'
import Books from '../components/pages/books/Index'
import Book from '../components/pages/books/Item'
import Authors from '../components/pages/authors/Index'
import Author from '../components/pages/authors/Item'
import Publishers from '../components/pages/publishers/Index'
import Publisher from '../components/pages/publishers/Item'
import Search from '../components/pages/Search'
import NotFound from '../components/pages/NotFound'

Vue.use(Router)

const rv = {
  template: '<router-view v-on:loadingStart="loadStart" v-on:loadingEnd="loadEnd"/>',
  methods: {
    loadStart () {
      this.$emit('loadingStart')
      // console.log('loadingStart in rv')
    },
    loadEnd () {
      this.$emit('loadingEnd')
      // console.log('loadingEnd in rv')
    }
  }
}

const router = new Router({
  routes: [
    {
      path: '/',
      component: Books
    },
    {
      path: '/books/:bookId',
      component: Book,
      props: true
    },
    {
      path: '/authors',
      component: rv,
      children: [
        {
          path: '',
          component: Authors
        },
        {
          path: ':authorId',
          component: Author,
          props: true
        }
      ]
    },
    {
      path: '/publishers',
      component: rv,
      children: [
        {
          path: '',
          component: Publishers
        },
        {
          path: ':publisherId',
          component: Publisher,
          props: true
        }
      ]
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/404',
      title: '404',
      component: NotFound
    },
    {
      path: '*',
      redirect: '/404'
    }
  ],
  mode: 'history'
})
export default router



