<template>
  <v-container fluid grid-list-lg>
    <vue-headful :title="pageTitle"/>
    <h1>{{pageTitle}}</h1>
    <div class="text-xs-center" v-show="page.totalPages>1">
      <v-pagination :length="page.totalPages" v-model="currentPage" @input="getBooks"></v-pagination>
    </div>
    <v-layout row wrap>
      <Book :book="item" v-for="(item, index) in page.content" :key="index"/>
    </v-layout>
  </v-container>
</template>

<script>
import PublisherService from '../../../services/PublisherService'
import BookService from '../../../services/BookService'
import Book from '../../parts/Book'

export default {
  props: ['publisherId'],
  computed: {
    pageTitle () {
      return this.publisher.name
    }
  },
  data () {
    return {
      publisher: {
        id: '',
        name: ''
      },
      page: {
        items: [],
        totalPages: 2
      },
      currentPage: 1
    }
  },
  methods: {
    getItem () {
      let context = this
      PublisherService.getItem(context, context.publisherId, (data) => {
        context.publisher = data
      })
    },
    getBooks () {
      let context = this
      BookService.getListByPublisher(context, context.currentPage, context.publisherId, (data) => {
        context.page = data
      })
    }
  },
  beforeMount () {
    this.getItem()
    this.getBooks()
  },
  components: {
    Book
  }
}
</script>

<style lang="scss" scoped>

</style>



